@import url('https://fonts.googleapis.com/css2?family=IBM+Plex+Mono:wght@200&family=Roboto+Mono:wght@500&display=swap');

.App {
  text-align: center;
  background-color: #212121;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
}

h1 {
  font-family: 'IBM Plex Mono', 'Consolas', Courier, monospace;
  font-weight: 200;
  font-size: 7em;
  margin: 0;
}

p {
  font-family: 'Roboto Mono';
  font-size: 1.2rem;
  font-weight: 500;
}

@media screen and (max-width: 580px) {
  div {
    font-size: 11vw;
  }
}

.icons {
  margin-top: 3em;
  display: flex;
  align-items: center;
  justify-content: space-around;
  width: 20rem;
}

.icons > a {
  width: 3.5rem;
  height: 3.5rem;
  opacity: 0.25;
  color: white;
  font-size: 2rem;
  border: solid;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 100%;
  border-width: 1px;
  transition: opacity 0.5s;
}

.icons > a:hover {
  opacity: 1;
}